<template>
<div class="col-xs-12 col-md-6">
        <div class="card">
          <div class="card-header">
            <strong>Locker Issues</strong>
          </div>
          <form v-if="ready_for_display" id='locker_form' class="form-horizontal" data-vv-scope="form2"  @submit.prevent="handleSubmit_locker('form2')" > 
          <div class="card-body card-block">           
              <div class="row form-group">
                <div class="col col-md-3"><label class=" form-control-label">Class</label></div>
                  <div class="col col-md-9" style='display:flex;flex-direction:row'>
                  <select name="Request_for" id="f" class="form-control" v-model="locker.form" style='width:5em'>
                        <option v-for="fl in formlevel">{{ fl }}</option>
                  </select>
                  <select name="Request_for" id="cls" class="form-control" v-model="locker.class" style='width:5em'>
                        <option v-for="fcls in formclass">{{ fcls }}</option>
                  </select>
                  </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label class=" form-control-label">Locker ID</label></div>
                  <div class="col-12 col-md-9">
                    <input type="number" id="locker_id" name="locker_id" class="form-control" v-model="locker.lockerid"/>
                  </div>
              </div>   
              <div class="row form-group">
                <div class="col col-md-3"><label class=" form-control-label">Description</label></div>
                  <div class="col-12 col-md-9">
                    <textarea placeholder="Enter Description" class="form-control" v-model="locker.description" required></textarea>
                  </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label class=" form-control-label">Follow-up Action</label></div>
                  <div class="col-12 col-md-9">
                    <textarea placeholder="Enter Description" class="form-control" v-model="locker.action" required></textarea>
                  </div>
              </div> 
              <div class="row form-group">               
                <div class="col col-md-3"><label class=" form-control-label">Status</label></div>
                <div class="col col-md-9">
                  <select name="Request_for" id="f" class="form-control" v-model="locker.status">
                        <option v-for="is in issue_stat">{{ is }}</option>
                  </select> 
                </div>  
              </div>       
          </div>
          <div class="card-footer">
            <button type="submit" class="btn btn-primary btn-sm">
              <i class="fa fa-paper-plane"></i> Update
            </button>
            <button type="reset" class="btn btn-danger btn-sm" onclick="window.history.go(-1); return false;">
              <i class="fa fa-ban"></i> Cancel
            </button>
          </div>
          </form>
        </div>  
</template>
<script>
export default {
    data(){
        return{
          ready_for_display:false,
            locker:{
                id:"",
                form:"",
                class:"",
                lockerid:"",
                description:"",
                created_by:"",
                action:"",
                status:""
            },
            fetchdata:[],
            formlevel:['F1','F2','F3','F4','F5'], 
            formclass:['A','B','C','D','E'],
            issue_stat:['Pending','Completed'], 
            req_name:"",
            return_msg:"",
        }
    },
    async created(){
        var id = document.getElementById('tmp_id').value;
        var formData = new FormData();
        formData.append('id', id);
        await axios.post('RequestPortal/backend/edit_locker.php',formData)
        .then(
          response=>{
            this.fetchdata = response.data
            console.log(this.fetchdata[0])
            this.locker.id = this.fetchdata[0].id
            this.locker.form = this.fetchdata[0].form
            this.locker.class = this.fetchdata[0].class
            this.locker.lockerid = this.fetchdata[0].lockerid
            this.locker.description = this.fetchdata[0].description
            this.locker.created_by = this.fetchdata[0].created_by
            this.locker.action = this.fetchdata[0].action
            this.locker.status = this.fetchdata[0].status          
          },
          this.ready_for_display = true
        )
    },
    mounted:function(){
        window.onbeforeunload = function (e) {
            e = e || window.event;
            if (e) {
                e.returnValue = 'Changes you made may not be saved.';
            }
            return 'Changes you made may not be saved.';
        }
    },
    destroyed(){
      window.onbeforeunload=null;
    },
    methods:{
        handleSubmit_locker(scope){
          var formData = new FormData();
          formData.append('id', this.locker.id);
          formData.append('form', this.locker.form);
          formData.append('class', this.locker.class);
          formData.append('lockerid', this.locker.lockerid);
          formData.append('Description', this.locker.description);
          formData.append('created_by', this.locker.created_by);
          formData.append('action', this.locker.action);
          formData.append('status', this.locker.status);
          axios.post('RequestPortal/backend/update_locker.php',formData)
          .then(
            response=>{
              this.return_msg = response.data
              if(this.return_msg=="success"){
                  this.$router.go(-1)
            }           
        }  
      )
    }
}
}
</script>



